.containerSuccess {
    font-size: 0;

    .header {
        width: 128px;
        height: 24px;
        margin-top: 12px;
        margin-left: 16px;
        background-image: url(../../assets/success/4399CloudClass.png);
        background-size: 128px 24px;
    }

    .content {
        display: inline-block;
        width: 375px;
        margin-top: 83px;
        text-align: center;

        .successIcon {
            display: inline-block;
            width: 72px;
            height: 72px;
            background-color: #00C176;
            border-radius: 50%;

            .tick {
                display: inline-block;
                width: 34.8px;
                height: 27.6px;
                margin-top: 22.2px;
                background-image: url(../../assets/success/tick.png);
                background-size: 34.8px 27.6px;
            }
        }

        .successMsg {
            margin-top: 26px;
            font-size: 18px;
            line-height: 25px;
        }

        .attention {
            width: 318px;
            margin: 0 auto;
            margin-top: 9px;
            line-height: 140%;
            font-size: 15px;
            color: #FF8018;
        }
    }
}