.finallContainerInputBox {
    border: 0;
}

.containerInputBox {
    position: relative;
    width: 339px;
    margin: 0 auto;
    font-size: 16px;
    border-bottom: 0.5px solid #E5E5E5;

    .starRed {
        color: red;
    }

    .starOpacity {
        opacity: 0;
    }

    .label {
        display: inline-block;
        width: 64px;
        height: 17px;
        margin-left: 3px;
        transform: translateY(-0.03rem);
        text-align: justify;
        vertical-align: middle;
        font-weight: bold;
    }

    .label::after {
        display: inline-block;
        width: 100%;
        content: '';
        overflow: hidden;
    }

    .input {
        width: 235px;
        height: 64px;
        margin-left: 30px;
        font-size: 16px;
        font-weight: normal;
    }

    .input::-webkit-input-placeholder {
        color: #E0E0E0;
        font-weight: bold;
    }

    .arrow {
        position: absolute;
        top: 25px;
        right: 0;
        display: inline-block;
        width: 14px;
        height: 14px;
        margin-left: 16px;
        background-image: url(../../assets/main/ic_arrow.png);
        background-size: 14px;
    }

    .errorInfo {
        height: 14px;
        font-size: 12px;
        color: #F36861;
        transform: translateY(-0.26rem);
    }

    .errorInfo::before {
        content: '';
        display: inline-block;
        width: 12px;
        height: 12px;
        margin-right: 3px;
        background-image: url(../../assets/main/ic_error.png);
        background-size: 12px;
        transform: translateY(0.023rem);
    }

    .otherBox {
        width: 100%;
        text-align: right;

        .otherInput {
            display: inline-block;
            width: 236px;
            font-size: 16px;
            line-height: 64px;
            border-top: 0.5px solid #E5E5E5;
            font-weight: normal;
        }

        .otherInput::-webkit-input-placeholder {
            color: #E0E0E0;
            font-weight: bold;
        }
    }

    .validationBtn {
        position: absolute;
        top: 15px;
        right: 0;
        width: 102px;
        height: 32px;
        font-size: 14px;
        color: #ffffff;
        font-weight: normal;

        .loading {
            display: inline-block;
            width: 16px;
            height: 16px;
            background-image: url(../../assets/main/ic_loading.png);
            background-size: 16px;
            animation: move 2s linear;
            -webkit-animation: move 2s linear;
            animation-iteration-count: 6;
            -webkit-animation-iteration-count: 6;
        }

        @keyframes move {
            from {
                transform: rotate(0)
            }

            to {
                transform: rotate(-360deg)
            }
        }
    }

    .validationAble {
        background-color: #00C176;
    }

    .validationUnable {
        background-color: #E5E5E5;
    }
}
