.toastBox {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    font-size: 0;
}

.toastMsg {
    position: absolute;
    display: inline-block;
    top: 50%;
    left: 50%;
    padding: 15px 20px;
    width: 307px;
    border-radius: 50px;
    color: #ffffff;
    background-color: rgba(0, 0, 0, .5);
    transform: translate3d(-50%, -50%, 0);
    font-size: 14px;
}
