* {
    font-family: Helvetica, Tahoma, Arial, sans-serif;
    font-weight: bold;
}

.containerMain {
    width: 375px;
    font-size: 0;
    background-color: #F7F7FB;

    .banner {
        width: 375px;
        height: 128px;
    }

    .content {
        width: 100%;

        .inputBox {
            margin-top: 10px;
            background-color: #ffffff;
        }

        .firstInputBox {
            margin-top: 0;
        }

        .formRequire {
            width: 100%;
            padding: 13px 18px;
            // font-weight: 500;
            background-color: #ffffff;

            .formTitle {
                font-size: 20px;
                line-height: 28px;
                color: #333333;
                font-weight: bold;

                .formSubTitle {
                    display: inline-block;
                    width: 78px;
                    font-size: 14px;
                    color: #999999;
                    text-align: center;
                    transform: translateY(-0.035rem);
                    font-weight: normal;

                    font {
                        color: red;
                    }
                }

                .formSubTitle::before {
                    content: '';
                    display: inline-block;
                    vertical-align: middle;
                    height: 100%;
                }
            }

        }

        .remindBox {
            background-color: #ffffff;
            padding: 0 18px;

            .remind {
                width: 100%;
                padding: 5px 15px 8px 15px;
                font-size: 14px;
                color: #FF8018;
                background-color: #FFF0E3;
                border-radius: 4px;
                line-height: 20px;

                .textLine1 {
                    line-height: 30px;
                }

                .textLine1::before {
                    content: '';
                    display: inline-block;
                    width: 16px;
                    height: 16.5px;
                    margin-right: 10px;
                    transform: translateY(0.05rem);
                    background-image: url(../../assets/main/ic_remind.png);
                    background-size: 16px 16.5px;
                }

                .textLine {
                    line-height: 23px;
                }
            }
        }


        .autoRegistPromptBox {
            width: 100%;
            padding: 14px 18px 1px 18px;
            background-color: #ffffff;

            .autoRegistPrompt {
                width: 100%;
                background-color: #FAFAFA;
                border-radius: 4px;
                font-size: 14px;
                line-height: 30px;
                color: #FF8018;

                .promptMsg {
                    margin-left: 16px;
                }
            }
        }
    }

    .footer {
        width: 100%;
        font-size: 12px;
        padding: 0 18px;
        background-color: #ffffff;

        .agreementCheck {
            width: 240px;
            margin: 0 auto;
            padding-top: 24px;
            line-height: 17px;

            .agreement {
                width: 30px;
                height: 100%;
                display: inline-block;
                vertical-align: top;

                .icon {
                    display: inline-block;
                    width: 12px;
                    height: 12px;
                    margin-top: 2.3px;
                    background-size: 12px;
                }

                .iconCheck {
                    background-image: url(../../assets/main/ic_check.png);
                }

                .iconChecked {
                    background-image: url(../../assets/main/ic_checked.png);
                }
            }



            .declare {
                display: inline-block;
                width: 210px;
            }
        }

        .submitBtn {
            width: 100%;
            height: 48px;
            margin-top: 16px;
            margin-bottom: 36px;
            background-color: #00C176;
            border-radius: 4px;
            font-size: 16px;
            // font-weight: 500;
            color: #FFFFFF;
        }
    }
}