.containerPicker {
    position: fixed;
    top: 0;
    left: 0;
    width: 375px;
    height: 100vh;
    background-color: rgba(0, 0, 0, .4);
    z-index: 999;

    .contentPicker {
        position: absolute;
        bottom: 0;
        width: 100%;
        font-size: 0;
        text-align: center;
        background-color: #ffffff;
        border-radius: 8px 8px 0 0;
        transition: all 0.2s linear;
        transform: translateY(7.5rem);

        button {
            width: 343px;
            height: 56px;
            margin: 0 auto;
            font-size: 16px;
            font-weight: normal;
            box-shadow: inset 0 -0.5px 0 #F1F1F1;
        }

        .pickerContent {
            max-height: 280px;
            overflow-y: scroll;

            .checked {
                color: #00C176;
            }

            .cancelBtn {
                width: 343px;
                height: 56px;
                margin: 0 auto;
                font-size: 16px;
                box-shadow: inset 0 -0.5px 0 #F1F1F1;
            }
        }

        .divider {
            width: 375px;
            height: 10px;
            background-color: #F7F7FB;
        }
    }
}